.text-center {
  text-align: center !important;
}

.text-primary {
  color: #53a665 !important;
}

.bg-white {
  background-color: #fff !important;
}

.p-relative {
  position: relative;
}

html {
  font-size: 14px;
  font-family: "OpenSans-Regular", sans-serif;
  color: #333333;
  background: url("../images/pattern@2x.png") repeat 0 0/5px 5px;
}

h2 {
  font-size: 1.5rem;
  font-family: "BreeSerif-Regular", sans-serif;
  font-weight: normal;
  line-height: 1.2;
  margin: 0;
}
h2.has-bg {
  font-size: 1.143rem;
  color: #000;
}
h2.has-bg span {
  background: linear-gradient(white, white) repeat-x;
  background-size: 100% 1em;
  background-position: 0 0.25em;
}

p {
  margin: 0;
}

header {
  position: relative;
  padding-top: 8px;
  margin-bottom: 1.5rem;
}
header:before {
  content: "";
  width: 100%;
  height: 34px;
  background-color: rgba(255, 255, 255, 0.73);
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: -1;
}
header .container {
  position: relative;
  z-index: 1;
}

.logo {
  width: 94px;
  display: block;
  margin-bottom: 30px;
}
.logo img {
  width: 100%;
}

.nav {
  list-style: none;
  padding: 0;
  margin: 0;
}
.nav a {
  display: block;
  font-size: 16px;
  font-family: "BreeSerif-Regular", sans-serif;
  color: #333333;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.2s ease;
  border-bottom: 3px solid #333333;
  padding: 2px 0;
}
.nav a:active {
  color: #676767;
  border-color: #676767;
}
.nav a.active {
  color: #53a665;
  position: relative;
  border-color: #53a665;
}

.striped {
  position: relative;
}
.striped .container {
  position: relative;
  z-index: 1;
}

.stripes, .stripes:before, .stripes:after {
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  background: url("../images/pattern-d@2x.png") repeat 0 0/5px 5px;
}
.stripes:before {
  content: "";
  top: 149px;
}
.stripes:after {
  content: "";
  top: 299px;
}

.link, .link-secondary, .link-primary, .link-primary--pos {
  display: inline-flex;
}
.link-primary, .link-primary--pos {
  padding: 1px 3px 3px;
  text-decoration: none;
  color: #fff;
  line-height: 1;
  background-color: #53a665;
}
.link-primary--pos {
  position: absolute;
  top: 88%;
  right: -10px;
  white-space: nowrap;
}
.link-primary:hover, .link-primary--pos:hover {
  background-color: #4b955b;
}
.link-primary:active, .link-primary--pos:active {
  background-color: #428450;
}
.link-secondary {
  color: #53a665;
}
.link-secondary:hover {
  text-decoration: none;
}
.link-secondary:active {
  color: #428450;
}

.icon, .icon--code, .icon--lab, .icon--pie {
  width: 24px;
  height: 24px;
  display: inline-flex;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}
.icon--pie {
  background-image: url("../images/pie.svg");
}
.icon--lab {
  width: 30px;
  height: 30px;
  background-image: url("../images/lab.svg");
}
@media screen and (min-width: 960px) {
  .icon--lab {
    margin-top: -6px;
  }
}
.icon--code {
  background-image: url("../images/code.svg");
}

.video-container {
  height: 145px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.video-container .logo {
  width: 75px;
  margin: 0 auto 3px;
}
.video-container video {
  width: 100%;
}
.video-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 1rem;
  font-size: 12px;
  text-align: center;
  pointer-events: none;
}

footer .container {
  position: relative;
}
footer .container:before {
  content: "";
  width: 100%;
  height: 2px;
  border-top: 1px solid #cccccc;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
}
footer .soc {
  width: 24px;
  height: 24px;
  margin: 0 7px;
}
footer .soc svg {
  width: 100%;
  fill: #333333;
  transition: 0.2s ease;
}
footer .soc:active svg {
  fill: #676767;
}
footer .copyright {
  font-size: 10px;
  color: #808080;
}

@media screen and (min-width: 576px) {
  html {
    font-size: 16px;
  }

  h2.has-bg {
    font-size: 1.5rem;
    color: #53a665;
  }

  header {
    padding: 0;
    margin-bottom: 3rem;
  }
  header:before {
    height: 100%;
  }

  .logo {
    width: 130px;
    margin: 0 auto 10px;
  }

  .nav {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .nav li {
    display: flex;
    align-items: center;
  }
  .nav li:not(:first-child):before {
    content: "/";
    color: #333333;
    padding: 0 10px;
  }
  .nav a {
    border: none;
  }
  .nav a.active {
    cursor: default;
  }
  .nav a.active:before {
    content: "";
    width: 15px;
    height: 5px;
    background: url("../images/arrow.svg") repeat;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
  }

  .video-container {
    height: 240px;
  }
  .video-container .logo {
    width: 130px;
    margin-bottom: 5px;
  }
  .video-text {
    font-size: 1rem;
  }
}
@media screen and (min-width: 960px) {
  .container {
    max-width: 960px;
  }

  header {
    margin-bottom: 1.5rem;
  }

  .logo {
    margin-bottom: 0;
  }

  .nav {
    justify-content: flex-end;
  }
  .nav a:not(.active):hover {
    color: #808080;
  }

  footer .soc {
    margin: 0 0 0 15px;
  }
  footer .soc:hover svg {
    fill: #808080;
  }
}